import React from 'react';

import styles from './home.module.css';

import ClubItem from './components/clubItem/clubItem';


const clubs = [
    {
        clubName: 'Ambassadors',
        clubDesc: 'club desc',
        clubType: 'Advised'
    },
    {
        clubName: 'Ambassadors',
        clubDesc: 'club desc',
        clubType: 'Advised'
    },
    {
        clubName: 'Ambassadors',
        clubDesc: 'club desc',
        clubType: 'Advised'
    },
    {
        clubName: 'Ambassadors',
        clubDesc: 'club desc',
        clubType: 'Advised'
    },
    {
        clubName: 'Ambassadors',
        clubDesc: 'club desc',
        clubType: 'Advised'
    },
    {
        clubName: 'Ambassadors',
        clubDesc: 'club desc',
        clubType: 'Advised'
    }
];

const home = () => {
    
    
    
    const clubList = clubs.map((club, index) => {
        return (
            <ClubItem
                key={index}
                clubName={club.clubName}
                clubDesc={club.clubDesc}
                clubType={club.clubType}
            />
        );
    });

    return (
        <div className='Home'>
            <h1 className={styles.header}>TAMS Clubs</h1>
            
            <hr />

            <div className={styles.wrapper}>
                {clubList}
            </div>
        </div>
    );
};

export default home